import './public-path';

import * as vue from 'vue';
import type { App as AppInstance } from 'vue';
import App from './App.vue';
import createRouter from './router';
import Antd from './plugins/ant';
import '@hwork/ant-design-vue/dist/antd.less';
import './global.less';

import pinia from '@/store';
import { useAppStore } from '@/store/app';
import { qiankunProps, WebEventEmitterType } from './types/qiankun';
import { userInfoCheckBeforeGuard, gioAfterGuard } from './router/guard';
import { checkPermission } from './api/permission';

import sentry from './plugins/sentry';

import { hworkAppReadyApi, isiOS } from './utils/util';

import { isHworkMobile } from '@deep/shared';
import { initVConsole } from './utils/vconsole';
import { initRecorder } from './utils/recorder';
import { initSearch } from './plugins/search';

initVConsole();

if (isHworkMobile() && isiOS()) {
    import('./hwork-ios.less');
}

const debounce = (callback: (...args: any[]) => void, delay: number) => {
    let tid: any;
    return function (...args: any[]) {
        const ctx = self;
        tid && clearTimeout(tid);
        tid = setTimeout(() => {
            callback.apply(ctx, args);
        }, delay);
    };
};

const _ = (window as any).ResizeObserver;
(window as any).ResizeObserver = class ResizeObserver extends _ {
    constructor(callback: (...args: any[]) => void) {
        callback = debounce(callback, 20);
        super(callback);
    }
};

window.Vue = vue;
const { createApp } = vue;

let instance: AppInstance | null = null;
let router = null;

const render = async (props: qiankunProps) => {
    const {
        container,
        historyType: propHistoryType,
        routerBase,
        mainAppRouter,
    } = props;

    let historyType = propHistoryType;
    if (routerBase) {
        historyType = 'history';

        const res = await checkPermission();
        if (!res.data.withinPurview) {
            mainAppRouter?.push('/portal/nopermission');
            return;
        }

        if (window.__IS_HWORK_QIANKUN__TERMINAL === 'web') {
            if (document.querySelector('.hwork-web-feedback')?.style) {
                document.querySelector('.hwork-web-feedback')!.style!.display =
                    'none';
            }
        }
    }

    instance = createApp(App);
    router = createRouter(historyType);
    // sentry
    // sentry.init(instance, router);

    instance.use(pinia).use(Antd).use(router);

    router.beforeEach(userInfoCheckBeforeGuard);
    router.afterEach(gioAfterGuard);

    const mountEl = container
        ? container.querySelector('#hwork-assistant-app')
        : document.querySelector('#hwork-assistant-app');

    const appStore = useAppStore();
    appStore.setHistoryType(historyType);
    appStore.setRootElement(mountEl as HTMLElement);
    appStore.setWebEventEmitter(props.EventEmitter as WebEventEmitterType);

    if (props.agent) {
        appStore.setAutoSend({ agent: props.agent, message: props.message });
    }

    initSearch(router);

    instance.mount(mountEl);

    // initRecorder(router);
};

// 独立运行时
if (!window.__POWERED_BY_QIANKUN__) {
    // history｜memory
    render({
        historyType: 'hash',
        baseUrl:
            process.env.VUE_APP_CLIENT === 'mobile'
                ? '/mobile'
                : process.env.BASE_URL,
    });
}

export async function bootstrap() {}
export async function mount(props: qiankunProps) {
    render({ ...props, historyType: 'memory' });
    // hwork桌面端js api全局挂载配置
    window.HworkJSApi = props.HworkJSApi;
}
export async function unmount(props: qiankunProps) {
    instance?.unmount?.();
    // instance?._container.innerHTML = '';
    instance = null;
    router = null;
    if (props.routerBase) {
        if (window.__IS_HWORK_QIANKUN__TERMINAL === 'web') {
            if (document.querySelector('.hwork-web-feedback')?.style) {
                document.querySelector('.hwork-web-feedback')!.style!.display =
                    'block';
            }
        }
    }
}

if (process.env.NODE_ENV === 'development') {
    if (window.location.href.includes('/oauth2/client/authorize')) {
        window.location.href = '/#/login' + window.location.search;
    }
}

document.addEventListener(
    'resume',
    function () {
        hworkAppReadyApi(() => {
            window.AlipayJSBridge.call('hideTitlebar');
        });
    },
    false,
);
