import { useAppStore } from '@/store/app';
import { getUrlParams } from '@/utils/util';
import { Router } from 'vue-router';

let originSearchHandler: (searchKey: string) => void;
let searchHandler: (searchKey: string) => void;
export function initSearch(router: Router) {
    const query = getUrlParams();
    const appStore = useAppStore();
    if (query.searchKey) {
        appStore.setAutoSend({ agent: 'super', message: query.searchKey });
    }
    if (window.hwork?.onActiveAIBot) {
        originSearchHandler = searchHandler = (searchKey: string) => {
            appStore.setAutoSend({ agent: 'super', message: searchKey });
            router.push('/chat');
        };
        window.hwork.onActiveAIBot((searchKey: string) => {
            searchHandler?.(searchKey);
        });
    }
}

export function changeSearchHandler(handler: (searckKey: string) => void) {
    if (window.hwork?.onActiveAIBot) {
        searchHandler = handler;
    }
}
export function resetSearchHandler() {
    if (window.hwork?.onActiveAIBot) {
        searchHandler = originSearchHandler;
    }
}
