export const chatGroupListLoading = ref<boolean>(true);

export async function queryChatGroupList(q?: string) {
    chatGroupListLoading.value = true;
    const { data } = await getChatGroupList(q);
    if (q) {
        chatGroupFilterList.value = data.list;
        // .sort((a, b) => {
        //     if (new Date(a.updatedAt) < new Date(b.updatedAt)) {
        //         return -1;
        //     }
        //     return 1;
        // });
    } else {
        chatGroupList.value = data.list;
        // .sort((a, b) => {
        //     if (new Date(a.updatedAt) < new Date(b.updatedAt)) {
        //         return -1;
        //     }
        //     return 1;
        // });
    }
    chatGroupListLoading.value = false;
}

