export async function addChatGroup(
    groupName: string,
    groupType: string,
    imgUrl: string,
    iconBackground: string,
    groupDesc?: string,
) {
    const res = await createChatGroup(
        groupName,
        groupType,
        imgUrl,
        iconBackground,
        groupDesc ?? '',
    );
    // const code = res.data.groupCode;
    // const type = res.data.groupType;

    // chatGroupList.value.push({
    //     ...res.data,
    //     groupDesc: groupDesc ?? '',
    //     lastChatTime: dayjs().format('YYYY-MM-DD HH:mm:ss'),
    // });

    changeChatGroup(res.data.groupCode);

    // queryChatGroupList();
    return res.data;
}

export async function addCommonChatGroup() {
    await addChatGroup('AI小助手', 'common', aiAssistantIcon, '');
}

