export async function clearChatGroups() {
    // await deleteChatGroup(chatGroupList.value.map((item) => item.groupCode));
    await clearUserChatGroups();
    chatGroupList.value = [];
    // await addChatGroup('AI小助手', 'common', aiAssistantIcon);
    // changeChatGroup(chatGroupList.value[0].groupCode);
}

export async function renameChatGroup(groupCode: string, groupName: string) {
    await updateChatGroup(groupCode, groupName);
    queryChatGroupList();
}

export const prompt = ref<string>();

export function setPrompt(value?: string) {
    prompt.value = value;
}

export async function pinChatGroup(groupCode: string) {
    await topChatGroup(groupCode);
    queryChatGroupList();
}

export async function unpinChatGroup(groupCode: string) {
    await untopChatGroup(groupCode);
    queryChatGroupList();
}

export async function removeChatGroupFromList(groupCode: string) {
    await removeChatGroup(groupCode);
    queryChatGroupList();
}
