<template>
    <a-layout
        class="chat-container"
        :class="{ web: isWeb, 'hwork-web-page': isHworkWebPage }"
        id="hwork-ai-assistant-wrapper"
    >
        <Sider v-if="!isMobile" v-show="expand" :chat-running="chatRunning" />
        <a-layout class="assistant-content-layout">
            <div
                class="sider-expand-button"
                @click="triggerExpandHandle"
                v-if="!isMobile"
            >
                <h-icon-left v-if="expand" />
                <h-icon-right v-else />
            </div>
            <router-view :key="$route.fullPath" />
        </a-layout>
    </a-layout>
</template>
<script setup lang="ts">
import { computed, onMounted, watch, KeepAlive, ref, provide } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { showMobile, showWeb } from '@/utils/util';
import Sider from './Sider.vue';
import { currentChatGroup, queryChatGroupList } from '@/hooks/useChatGroupList';
import { useAppStore } from '@/store/app';
import useExpand from '@/hooks/useExpand';
import { isHworkMobile } from '@deep/shared';
const aiAssistantIcon = process.env.BASE_URL + 'icon-assistant.svg';

const isMobile = showMobile();
const isWeb = showWeb();
const route = useRoute();
const router = useRouter();
const appStore = useAppStore();
const [expand, triggerExpand] = useExpand();

const chatRunning = ref<boolean>(false);
provide('chatRunning', chatRunning);

const isHworkWebPage = computed(() => {
    return (
        window.__IS_HWORK_QIANKUN__TERMINAL === 'web' &&
        appStore.historyType === 'history'
    );
});

const triggerExpandHandle = () => {
    if (typeof triggerExpand === 'function') triggerExpand();
};

watch(currentChatGroup, () => {
    if (!currentChatGroup.value) return;
    const agent =
        currentChatGroup.value?.groupType === 'common'
            ? 'super'
            : currentChatGroup.value?.groupType;
    router.push(`/chat/${currentChatGroup.value?.groupCode}?agent=${agent}`);
});

watch(route, () => {
    if (route.name === 'chat.new') {
        currentChatGroup.value = undefined;
    }
});

onMounted(() => {
    queryChatGroupList();
    if (!isHworkMobile()) {
        document.addEventListener(
            'visibilitychange',
            function (e) {
                if (document.visibilityState == 'visible') {
                    queryChatGroupList();
                }
            },
            false,
        );
    }
});
</script>
