.@{ant-prefix}-layout {
    background: transparent;
}
.assistant-page-container {
    height: 100%;
    width: 100%;
    position: relative;
}
.assistant-content-layout {
    position: relative;
    background: url('@/assets/images/bg.png') no-repeat;
    background-size: 100% 100%;
}
.chat-container {
    max-height: 100vh;
    min-height: 100vh;
    // &.web {
    //     min-height: 600px;
    // }
}
.hwork-web-page {
    min-height: calc(100vh - 48px);
    max-height: calc(100vh - 48px);
}

.assistant-content-layout {
    position: relative;
    .sider-expand-button {
        position: absolute;
        z-index: 1;
        height: 48px;
        width: 20px;
        border-top-right-radius: 12px;
        border-bottom-right-radius: 12px;
        background-color: #fff;
        box-sizing: 0 4px 16px 0 rgba(2, 9, 25, 0.04);
        display: flex;
        align-items: center;
        justify-content: center;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        font-size: 16px;
        color: #4e5868;
        cursor: pointer;
        &:hover {
            color: #86909b;
        }
    }
}
</style>
