import { useAppStore } from '@/store/app';
import { isHworkDesktop, isMobile } from '@/utils/util';

type cachedEventType = {
    eventId: string;
    params: Record<string, string>;
};

class Gio {
    device: 'mobile' | 'desktop' | 'web' = 'web';
    userId: string = '';
    userName: string = '';

    // 是否已经初始化
    initialized: boolean = document.visibilityState === 'visible';
    cachedEvents: Array<cachedEventType> = [];

    constructor() {
        this.device = isMobile()
            ? 'mobile'
            : isHworkDesktop()
            ? 'desktop'
            : 'web';

        // 特殊处理桌面端上报
        if (this.device === 'desktop' && this.initialized === false)
            this.visibilityState();
    }

    getUserInfo() {
        const appStore = useAppStore();
        this.userId = appStore.userId;
        this.userName = appStore.userName;
    }

    /**
     * 事件上报
     * @param eventId 事件标识
     * @param payload 额外信息
     */
    track(eventId: string, payload?: Record<string, string>) {
        if (this.userId === '') {
            this.getUserInfo();
        }
        const params = {
            ...payload,
            devicetype: this.device,
            userId: this.userId,
            userName: this.userName,
        };
        if (this.device === 'mobile') {
            window.AlipayJSBridge?.call?.('gioTrack', {
                eventId,
                eventData: params,
            });
        } else if (this.device === 'desktop') {
            this.desktopTrack(eventId, params);
        } else {
            window.HworkJSApi?.gioTrack?.(eventId, params);
        }
    }

    desktopTrack(eventId: string, params: Record<string, string>) {
        if (document.visibilityState === 'visible' || this.initialized) {
            window.hwork?.track?.(eventId, params);
        } else {
            this.cachedEvents.push({
                eventId,
                params,
            });
        }
    }

    visibilityState() {
        document.addEventListener('visibilitychange', () => {
            if (document.visibilityState === 'visible') {
                this.initialized = true;
                this.cachedEvents.forEach(({ eventId, params }) => {
                    this.desktopTrack(eventId, params);
                });
                this.cachedEvents = [];
            }
        });
    }
}

export default new Gio();
