import gio from '@/plugins/gio';
import { getUserInfo } from '@/api/user';
import { useAppStore } from '@/store/app';
import { NavigationGuardNext, RouteLocationNormalized } from 'vue-router';
import { hworkAppReadyApi } from '@/utils/util';

export const gioAfterGuard = (to: RouteLocationNormalized) => {
    if (to.meta?.trackEventId) {
        const eventId = to.meta.trackEventId;
        gio.track(eventId as string);
    }
};

export const userInfoCheckBeforeGuard = (
    _to: RouteLocationNormalized,
    _form: RouteLocationNormalized,
    next: NavigationGuardNext,
) => {
    const appStore = useAppStore();
    hworkAppReadyApi(() => {
        window.AlipayJSBridge.call('hideTitlebar');
    });
    if (appStore.userId) {
        next();
    } else {
        getUserInfo()
            .then((res) => {
                appStore.setUserInfo(res.data);
            })
            .finally(() => {
                next();
            });
    }
};
