<template>
    <ConfigProvider
        :get-popup-container="getContainer"
        prefix-cls="ai-assistant-ant"
        :locale="zhCN"
    >
        <div :class="isWindows() && 'hwork-assistant-os-windows'">
            <!-- <layout> -->
            <WebModal v-if="appStore.isMemoryMode" />
            <router-view v-else />
            <!-- </layout> -->
            <div ref="quickappContainer"></div>
        </div>
    </ConfigProvider>
</template>
<script setup lang="ts">
import { isWindows } from '@/utils/util';
import { getContainer } from '@/utils/util';
import { ConfigProvider, message, Modal } from '@hwork/ant-design-vue';
import zhCN from '@hwork/ant-design-vue/es/locale/zh_CN';
import { ref, onMounted } from 'vue';
import WebModal from './layouts/web-modal.vue';
import VConsole from 'vconsole';
import { useAppStore } from './store/app';
import { parseTokenGetUserInfoAsync } from '@deep/shared';

const appStore = useAppStore();
const quickappContainer = ref();

// const isQiankunSub = ref<boolean>(
//     !!window.__POWERED_BY_QIANKUN__ &&
//         window.__IS_HWORK_QIANKUN__TERMINAL === 'web' &&
//         appStore.historyType === 'memory',
// );

window.__ASSISTANT_ENV__ = process.env.VUE_APP_RUN_ENV || 'stage';

message.config({
    getContainer: getContainer,
});

Modal.defaultProps = {
    ...Modal.defaultProps,
    getContainer,
};

onMounted(async () => {
    const userInfo = await parseTokenGetUserInfoAsync();
    if (
        ['19018169', '01442042', '20113952'].includes(userInfo?.id || '') &&
        process.env.VUE_APP_RUN_ENV !== 'dev'
    ) {
        new VConsole();
    }
    appStore.setQuickappContainer(quickappContainer.value);
});
</script>
<style lang="less">
.hwork-assistant-os-windows {
    ::-webkit-scrollbar-track {
        border-radius: 8px;
        background-color: rgba(0, 0, 0, 0) !important;
    }

    ::-webkit-scrollbar {
        width: 8px;
        height: 8px;
    }

    ::-webkit-scrollbar-thumb {
        border-radius: 8px;
        background-color: rgba(0, 0, 0, 0.16) !important;
    }
}
</style>
