import {
    createRouter,
    RouteRecordRaw,
    createWebHashHistory,
    createMemoryHistory,
} from 'vue-router';
import Layout from '@/layouts/index.vue';

const routes: Array<RouteRecordRaw> = [
    {
        path: '/',
        name: 'home',
        redirect: '/chat',
        component: Layout,
        children: [
            {
                path: '/chat',
                name: 'chat.exact',
                component: () =>
                    import(
                        /* webpackChunkName: "chat-exact" */ '../views/Chat/exact.vue'
                    ),
            },
            {
                path: '/chat/new',
                name: 'chat.new',
                meta: {
                    trackEventId: 'AovLJ3YR_017046',
                },
                component: () =>
                    import(
                        /* webpackChunkName: "chat-new" */ '../views/Chat/index.vue'
                    ),
            },
            {
                path: '/chat/history',
                name: 'chat.history',
                component: () =>
                    import(
                        /* webpackChunkName: "chat-history" */ '../views/Mobile/ChatHistory.vue'
                    ),
            },
            {
                path: '/chat/:code',
                name: 'chat.code',
                meta: {
                    trackEventId: 'AovLJ3YR_017046',
                    keepAlive: true,
                },
                component: () =>
                    import(
                        /* webpackChunkName: "chat" */ '../views/Chat/index.vue'
                    ),
            },
            {
                path: '/market',
                name: 'market',
                meta: {
                    trackEventId: 'AovLJ3YR_017045',
                },
                component: () =>
                    import(
                        /* webpackChunkName: "market" */ '../views/Market.vue'
                    ),
            },
            {
                path: '/create/assistant',
                name: 'create.assistant',
                component: () =>
                    import(
                        /* webpackChunkName: "create-assistant" */ '../views/Mobile/CreateAssistantMobile/index.vue'
                    ),
            },
            {
                path: '/skill/:code',
                name: 'assistant.skill',
                component: () =>
                    import(
                        /* webpackChunkName: "create-assistant" */ '../views/Mobile/SkillMap.vue'
                    ),
            },
            {
                path: '/recorder',
                name: 'recorder',
                component: () =>
                    import(
                        /* webpackChunkName: "recorder" */ '../views/Recorder.vue'
                    ),
            },
        ],
    },

    {
        path: '/login',
        name: 'login',
        component: () =>
            import(/* webpackChunkName: "login" */ '../views/Login.vue'),
    },
];

// const router = createRouter({
//     history: createWebHistory(process.env.BASE_URL),
//     routes,
// });

export default (historyType = 'hash', baseUrl = process.env.BASE_URL) => {
    return createRouter({
        history:
            historyType === 'memory'
                ? createMemoryHistory(baseUrl)
                : createWebHashHistory(baseUrl),
        routes,
    });
};
