// 停止询问
export function stopMessage(chatGroupCode: string, agentTaskId: string) {
    return request<Res<string>>({
        url: '/api/ai-assistant/v1/ai-chat/chat-messages-stop',
        method: 'post',
        data: {
            chatGroupCode,
            agentTaskId,
        },
    });
}

