// 查询会话聊天记录
export function getChatRecords(
    groupCode: string,
    offsetId?: number,
    pageSize?: number,
) {
    // return new Promise<Res<{ list: ChatRecord[] }>>((resolve) => {
    //     resolve(records);
    // });
    return request<Res<{ list: ChatRecord[]; page: number; pageSize: number }>>(
        {
            url: '/api/ai-assistant/v2/ai-chat/get-records',
            params: {
                groupCode,
                offsetId,
                page: 1,
                pageSize: pageSize ?? 10,
            },
        },
    );
}

export function updateChatRecord(
    recordId: number,
    msgType: string,
    cardTemplateIdNew: string,
    groupCode: string,
    cardTemplateVar?: Record<string, any>,
) {
    const data: any = {
        recordId,
        msgType,
        groupCode,
    };
    if (cardTemplateIdNew) {
        data.cardTemplateIdNew = cardTemplateIdNew;
    }
    if (cardTemplateVar && Object.keys(cardTemplateVar).length) {
        data.cardTemplateVar = cardTemplateVar;
    }
    return request({
        url: '/api/ai-assistant/v1/ai-chat/update-record',
        method: 'post',
        data,
    });
}

// 删除会话
export function deleteChatGroup(groupCodes: string | string[]) {
    let data;
    if (typeof groupCodes === 'string') {
        data = {
            groupCode: groupCodes,
        };
    } else {
        data = {
            groupCodes: groupCodes,
        };
    }
    return request({
        url: '/api/ai-assistant/v1/ai-chat/del-chat-group',
        method: 'post',
        data,
    });
}

