<template>
    <component :is="cardComponent" :chatGroup="props.chatGroup"></component>
</template>
<script setup lang="ts">
import { computed, defineProps } from 'vue';
import { showMobile } from '@/utils/util';
import { ChatGroup } from '@/api/chat';
import ChatCard from './ChatCard.vue';
import ChatCardMobile from './ChatCardMobile.vue';
const props = defineProps<{
    chatGroup: ChatGroup;
}>();

const isMobile = showMobile();
const cardComponent = computed(() => {
    return isMobile ? ChatCardMobile : ChatCard;
});
</script>
