import { message } from '@hwork/ant-design-vue';
import {
    parsePathToObject,
    isHttpOrHttpsUrl,
    isFunction,
    handleCloseAi,
    isMobile,
    isHworkDesktop,
    // isImage,
    // getContainer,
} from './util';
import renderQuickApp from '@deep/quickapp';

const miniProgramMap = {
    todo: '2022032414200001',
    calendar: '2022062300000001',
    book_meeting_room: '20221111',
};

export type QuickAppData = {
    mountTarget: HTMLElement;
    cardFormInfo?: Record<string, unknown>;
    originTemplateInfo?: Record<string, unknown>;
};

/**
 * https://docs.hwork.haier.net/weboffice/l/scNzZEoD8XA2n
 * 桌面端菜单切换，
 * 工作台: workbench
 * 消息: im
 * 待办: task
 * 会议: meeting
 * 日历: schedule
 * 文档: cloud
 * AI助手: aiassistant
 */
export type DesktopMenu =
    | 'workbench'
    | 'im'
    | 'task'
    | 'meeting'
    | 'schedule'
    | 'cloud'
    | 'aiassistant';

export type DesktopLinkConfing = {
    // 两个方法，code参数不一致。。
    openInHworkMenu?: 'work' | 'todo';
    tabName?: string;
};

export const HWORK_LINK_URL = 'https://hwork-link.haier.net';
// 轻应用
export const QUICK_APP_URL =
    'https://hwork-link.haier.net/client/quick_app/open';
// 离线包&小程序
export const MINI_PROGRAM_URL =
    'https://hwork-link.haier.net/client/mini_program/open';
// 原生应用
export const NATIVE_APP_URL =
    'https://hwork-link.haier.net/client/native_app/open';

// ============================平台内置能力 start================================
// 打开日历
export const CALENDAR_URL = 'https://hwork-link.haier.net/client/calendar/open';
// 打开待办
export const TODO_URL = 'https://hwork-link.haier.net/client/todo/open';

// 线上会议
export const MEETING_URL = 'https://hwork-link.haier.net/client/meeting/open';

// 会议室预定
export const BOOK_MEETING_URL =
    'https://hwork-link.haier.net/client/meeting/book_room';

// 打开云文档
// export const CLOUD_DOCS_URL = 'https://hwork-link.haier.net/client/docs/open';
// ============================平台内置能力 end================================

export class HworkLink {
    isHworkLink(url: string) {
        return url.startsWith(HWORK_LINK_URL);
    }

    isQuickLink(url: string) {
        return url.startsWith(QUICK_APP_URL);
    }

    isMiniProgram(url: string) {
        return url.startsWith(MINI_PROGRAM_URL);
    }

    isNativeApp(url: string) {
        return url.startsWith(NATIVE_APP_URL);
    }

    // 日历
    isCalendarLink(url: string) {
        return url.startsWith(CALENDAR_URL);
    }

    // 待办
    isTodoLink(url: string) {
        return url.startsWith(TODO_URL);
    }

    // 线上会议
    isMeetingLink(url: string) {
        return url.startsWith(MEETING_URL);
    }

    // 会议室预定
    isBookRoomLink(url: string) {
        return url.startsWith(BOOK_MEETING_URL);
    }

    handleDesktopLink(url: string, config?: DesktopLinkConfing) {
        if (isFunction(window.hwork?.changeMenuOpenTab)) {
            const defaultParams: DesktopLinkConfing = {
                openInHworkMenu: 'work', // work 切换到工作台 todo 待办 docs 云文档
            };

            window.hwork.changeMenuOpenTab({
                url,
                ...defaultParams,
                ...config,
            });
        } else {
            message.warning('桌面端版本过低，请升级至最新版本');
        }
    }

    handleDesktopLinkToWeb(page: string) {
        if (isFunction(window.hwork?.openCustomModal)) {
            window.hwork.openCustomModal({
                page,
                type: 'web',
            });
        } else {
            message.warning('桌面端版本过低，请升级至最新版本');
        }
    }

    handleMobileLink(url: string) {
        // 跳转成功关闭ai
        window.AlipayJSBridge?.call?.('goToPage', { url }, () => {
            setTimeout(handleCloseAi, 300);
        });
    }
    
    handleMobileSearchLink(url: string) {
        window.AlipayJSBridge?.call?.('goToPage', { url });
    }

    handleDesktopMenuChange(menu: DesktopMenu) {
        window.hwork?.active(menu);
    }

    handleWebLink(url: string, target = '_blank') {
        let fullUrl = url;
        // 非完整路径，默认hwork域名访问
        if (!isHttpOrHttpsUrl(url)) {
            url = `/${url}`.replace(/^\/{2,}/, '/');
            fullUrl = `${process.env.VUE_APP_API_HWORK_BASE_URL}${url}`;
        }
        window.open(fullUrl, target);
    }

    handleNormalLink(url: string) {
        if (isMobile()) {
            this.handleMobileLink(url);
        } else if (isHworkDesktop()) {
            this.handleDesktopLink(url);
        } else {
            this.handleWebLink(url);
        }
    }

    // // 如果是图片
    // handleImageView(url: string) {
    //     if (isMobile()) {
    //         // this.handleMobileLink(url);
    //         window.AlipayJSBridge?.call?.('previewImage', {
    //             index: 0,
    //             imageUrlList: [url],
    //         });
    //     } else {
    //         // todo
    //     }
    // }

    analyzeMiniProgramUrl(url: string) {
        const { path, appId } = parsePathToObject(url);

        const param: { page?: string } = {};
        if (path) {
            try {
                const page = decodeURIComponent(path);
                param.page = page;
            } catch (e) {
                //empty
            }
        }
        return {
            appId,
            param,
        };
    }

    // 打开小程序
    openMiniProgram(appId: string, param = {}) {
        console.info('openMiniProgram', appId, param);
        if (!appId) return;
        handleCloseAi();
        window.AlipayJSBridge?.call?.('startApp', {
            appId,
            param,
        });
    }

    // 访问移动端原生能力
    openMobileNative(ability: string) {
        if (!ability) return;
        handleCloseAi();
        window.AlipayJSBridge?.call?.(ability, {});
    }

    serializeQuickAppData(url: string, quickAppData: QuickAppData) {
        const { cardFormInfo, originTemplateInfo, mountTarget } = quickAppData;
        const params = parsePathToObject(url);

        const appData: {
            cardFormInfo?: Record<string, unknown>;
            urlExtraInfo?: Record<string, unknown>;
            originTemplateInfo?: Record<string, unknown>;
        } = {
            cardFormInfo: cardFormInfo,
            originTemplateInfo,
            urlExtraInfo: params,
        };

        return {
            appData,
            mountTarget,
            appId: params.appId,
            mode: params.mode,
        };
    }

    renderQuickApp(url: string, config?: QuickAppData) {
        const { appId, appData, mountTarget } = this.serializeQuickAppData(
            url,
            config || {
                mountTarget: document.body,
            },
        );

        return renderQuickApp({
            appId,
            appData,
            // 当前只有pre和prod
            env: process.env.VUE_APP_RUN_ENV,
            mountTarget,
        });
    }
    trigger(url: string, config?: QuickAppData) {
        // 不合法
        if (!isHttpOrHttpsUrl(url)) {
            return console.warn('请检查url是否合法:', url);
        }
        // 非hworklink
        if (!this.isHworkLink(url)) {
            return this.handleNormalLink(url);
        }

        // 轻应用渲染
        if (this.isQuickLink(url)) {
            return this.renderQuickApp(url, config as QuickAppData);
        }

        // 线上会议
        if (this.isMeetingLink(url)) {
            return this.openMeeting();
        }

        // 会议室预定
        if (this.isBookRoomLink(url)) {
            return this.openMeetingRoom();
        }

        // 待办
        if (this.isTodoLink(url)) {
            return this.openTodo();
        }
        // 日历
        if (this.isCalendarLink(url)) {
            return this.openCalender();
        }

        // 移动端小程序跳转
        if (this.isMiniProgram(url)) {
            const { appId, param } = this.analyzeMiniProgramUrl(url);
            return this.openMiniProgram(appId, param);
        }

        // 原生应用
        if (this.isNativeApp(url)) {
            const { appId } = this.analyzeMiniProgramUrl(url);
            return this.openMobileNative(appId);
        }
    }

    // ============================平台内置能力 START================================
    // 打开视频会议
    openMeeting() {
        if (isMobile()) {
            this.openMobileNative('openMeeting');
        } else if (isHworkDesktop()) {
            this.handleDesktopMenuChange('meeting');
        } else {
            // web端不支持该功能
            message.warning('请前往h-work应用使用此功能');
        }
    }

    // 打开日历
    openCalender() {
        if (isMobile()) {
            this.openMiniProgram(miniProgramMap.calendar);
        } else if (isHworkDesktop()) {
            this.handleDesktopMenuChange('schedule');
        } else {
            this.handleWebLink('/portal/calendar-h5/calendar');
        }
    }
    // 打开待办
    openTodo() {
        if (isMobile()) {
            this.openMiniProgram(miniProgramMap.todo);
        } else if (isHworkDesktop()) {
            this.handleDesktopMenuChange('task');
        } else {
            this.handleWebLink('/portal/task/taskCenter');
        }
    }

    // 会议室预定
    openMeetingRoom() {
        if (isMobile()) {
            this.openMiniProgram(miniProgramMap.book_meeting_room);
        } else if (isHworkDesktop()) {
            this.handleDesktopLink(
                `${process.env.VUE_APP_API_HWORK_BASE_URL}/portal/jshyy/ChamberReserve`,
                {
                    tabName: '会议室预定',
                    openInHworkMenu: 'work',
                },
            );
        } else {
            this.handleWebLink('/portal/jshyy/ChamberReserve');
        }
    }
    // ============================平台内置能力 END================================
}

export default new HworkLink();
