import { getUserInfo, UserInfoType } from '@/api/user';
import { WebEventEmitterType } from '@/types/qiankun';
import { defineStore } from 'pinia';
import sentry from '@/plugins/sentry';
import { set } from '@vueuse/core';
export interface AppState {
    historyType: string | null;
    rootElement: HTMLElement | null;
    webEventEmitter: WebEventEmitterType | null;
    quickappContainer: HTMLElement | null;
    userInfo: UserInfoType | null;
    autoSend: {
        agent: string;
        message: string;
    } | null;
}

export const useAppStore = defineStore('app', {
    state: (): AppState => {
        return {
            historyType: null,
            rootElement: null,
            webEventEmitter: null,
            quickappContainer: null,
            userInfo: null,
            autoSend: null,
        };
    },
    actions: {
        setHistoryType(historyType: string) {
            this.historyType = historyType;
        },
        setRootElement(rootElement: HTMLElement) {
            this.rootElement = rootElement;
        },
        setWebEventEmitter(emitter: WebEventEmitterType) {
            this.webEventEmitter = emitter;
        },
        setQuickappContainer(container: HTMLElement) {
            this.quickappContainer = container;
        },
        setUserInfo(userInfo: UserInfoType) {
            this.userInfo = userInfo;
            // sentry
            sentry.setUserInfo(userInfo.userCode, userInfo.userName);
        },
        setAutoSend({
            agent,
            message = '',
        }: {
            agent: string;
            message?: string;
        }) {
            this.autoSend = {
                agent,
                message,
            };
        },
        clearAutoSend() {
            this.autoSend = null;
        },
    },
    getters: {
        isMemoryMode: (state) => state.historyType === 'memory',
        userId: (state) => state.userInfo?.userCode || '',
        userName: (state) => state.userInfo?.userName || '',
        userAvatar: (state) => state.userInfo?.userPhoto || '',
    },
});
