<template>
    <a-layout-sider width="280px" class="chat-sider">
        <!-- <BlockTitle title="对话列表" class="chat-sider-title mt16" /> -->
        <!-- <div class="operations-wrap">
            <template v-if="!isSearch">
                <div class="create-btn operations-btn" @click="newChatGroup">
                    <h-icon-plus-circle class="plus-icon" />
                    新建
                </div>
                <div
                    class="search-btn operations-btn"
                    @click.prevent.stop="enterSearch"
                >
                    <h-icon-search class="search-icon" />
                </div>
            </template>
            <a-input
                v-else
                v-model:value="searchText"
                ref="searchRef"
