                <template #suffix>
                    <h-icon-search
                        class="search-btn-search"
                        @click="searchFn"
                    />
                </template>
            </a-input>
        </div> -->
        <template v-if="!showSearch">
            <div class="market-btn operations-btn mt16" @click="toMarket">
                <div class="robot-icon"></div>
                智能体
            </div>
            <ChatList />
            <ChatListOperations />
        </template>
        <template v-else>
            <div class="search-wrap">
                <div v-if="chatGroupFilterList.length">
                    <template v-for="item in chatGroupFilterList">
                        <ChatCard
                            :chatGroup="item"
                            :isInSearch="true"
                            @click="confirmSearch(item.groupCode)"
                        />
                    </template>
                </div>
                <div v-else class="empty-wrap">
                    <img src="@/assets/icons/icon-no-search.svg" />
                    <div>无搜索结果</div>
                </div>
            </div>
        </template>
    </a-layout-sider>
</template>
<script setup lang="ts">
import '@hwork/icon/plus-circle';
import '@hwork/icon/search';
import { ref, nextTick } from 'vue';
import { BlockTitle } from '@deep/vue3';
import ChatList from '@/components/ChatList.vue';
import ChatListOperations from '@/components/ChatListOperations.vue';
import ChatCard from '@/components/ChatCard/index.vue';
import {
    changeChatGroup,
    currentChatGroup,
    chatGroupFilterList,
    queryChatGroupList,
} from '@/hooks/useChatGroupList';
import { useRouter } from 'vue-router';
import { useDebounceFn } from '@vueuse/core';
import { message } from '@hwork/ant-design-vue';

const props = defineProps<{ chatRunning: boolean }>();

const router = useRouter();
const isSearch = ref(false);
const searchText = ref('');
const showSearch = ref(false);
const searchRef = ref<HTMLElement>();
const enterSearch = () => {
    isSearch.value = true;
    nextTick(() => {
        window.addEventListener('click', blurExitSearch);
        searchRef.value?.focus();
    });
};
const exitSearch = () => {
    isSearch.value = false;
    showSearch.value = false;
    chatGroupFilterList.value = [];
    searchText.value = '';
    window.removeEventListener('click', blurExitSearch);
};
const confirmSearch = (code: string) => {
    changeChatGroup(code);
};
const blurExitSearch = (e: Event) => {
    e.preventDefault();
    const queryEl = document.querySelector(
        '.ai-assistant-ant-input-affix-wrapper',
    );
    nextTick(() => {
        if (!queryEl?.contains(e.srcElement as Node)) {
            exitSearch();
        }
    });
};
const searchFn = useDebounceFn((e) => {
    if (searchText.value) {
        showSearch.value = true;
        queryChatGroupList(searchText.value);
    } else {
        showSearch.value = false;
        chatGroupFilterList.value = [];
    }
}, 200);
async function newChatGroup() {
    // const chatGroup = await addChatGroup('AI小助手', 'common', aiAssistantIcon);

    router.push({
        path: '/chat/new',
        query: {
            agent: 'super',
        },
    });
    // router.push(`/chat/${chatGroup.groupCode}?type=${chatGroup.groupType}`);
}
const toMarket = () => {
    if (props.chatRunning) {
        message.info('正在生成中，请先停止回答');
        return;
    }
    currentChatGroup.value = undefined;
    router.push({
        path: `/market`,
    });
};
</script>
