import {
    isHworkMobile,
    isHworkDesktop,
    isHworkLite,
    isHworkPC,
    getTokenAsync as getTokenAsyncRaw,
} from '@deep/shared';
import { useAppStore } from '@/store/app';
import appLink from './appLink';

const toString = Object.prototype.toString;

export { isHworkDesktop };

export const getTokenAsync = async () => {
    try {
        return isFunction(window.$bridge?.getHworkToken)
            ? window.$bridge.getHworkToken()
            : getTokenAsyncRaw();
    } catch (err) {
        console.error('H-work AI助手获取token失败', err);
        Promise.reject(err);
    }
};

export const isFunction = (val: unknown) =>
    toString.call(val).toLocaleLowerCase() === '[object function]';

export const isImage = (val: string) => {
    const reg = /\.(jpg|jpeg|png|gif|bmp|webp)$/i;
    return reg.test(val);
};

export const isMobile = () => {
    const regex =
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i;
    return regex.test(window.navigator.userAgent);
};
// 初始化操作
export const hworkAppReadyApi = (callback: any) => {
    // 如果 jsbridge 已经注入则直接调用
    if (window.AlipayJSBridge) {
        callback && callback();
    } else {
        // 如果没有注入则监听注入的事件
        document.addEventListener('AlipayJSBridgeReady', callback, false);
    }
};

// 是否hwork零级菜单
export const isHworkInline = () => {
    return window?.hwork?.getModalPageParams?.('type') === 'inline';
};
export const showMobile = () => {
    // isHworkMobile 移动端 app
    // isMobile 移动端 web
    if (isHworkInline()) {
        return false;
    }
    return (
        isMobile() ||
        isHworkMobile() ||
        ((isHworkLite() || isHworkPC()) && window.innerWidth < 760)
    );
};
export const showWeb = () => {
    return !showMobile() && !isHworkDesktop();
};

export const getContainer = () => {
    const appStore = useAppStore();
    return appStore.rootElement || document.body;
};

export const geDrawerContainer = () => {
    const appStore = useAppStore();
    return (
        document.getElementById('hwork-assistant-app') ||
        appStore.rootElement ||
        document.body
    );
};

export const handleCloseAi = () => {
    if (isHworkDesktop()) {
        window?.parent?.hwork?.closeAiassistantView();
    } else if (window.__POWERED_BY_QIANKUN__) {
        const appStore = useAppStore();
        appStore.webEventEmitter?.emit('closeWebAI');
    } else if (isMobile()) {
        hworkAppReadyApi(() => {
            window.AlipayJSBridge.call(
                'hideAIAssistant',
                {},
                (res: unknown) => {
                    console.log('离线包隐藏AI助手:', res);
                },
            );
        });
    } else {
        console.log('handleCloseAi：其他');
    }
};

// 判断设备环境是否为Android
export const isAndroid = (): boolean => {
    const u = navigator.userAgent;
    return (
        u.indexOf('Android') > -1 ||
        u.indexOf('Adr') > -1 ||
        u.indexOf('Linux') > -1
    );
};

// 判断设备环境是否为Android
export const isiOS = (): boolean => {
    const regex = /(phone|pad|pod|iPhone|iPod|ios|iPad)/i;
    return regex.test(window.navigator.userAgent);
};
// 吸附在桌面端内关闭小助手
export const closeAIView = () => {
    window?.parent?.hwork?.closeAiassistantView();
};

// 打开小助手独立窗口
export const openStandaloneWindow = () => {
    window?.parent?.hwork?.openAiassistantWindow('openStandaloneWindow');
    closeAIView();
};

export const isWindows = () => {
    const agent = navigator.userAgent.toLowerCase();
    if (agent.indexOf('win32') >= 0 || agent.indexOf('wow32') >= 0) {
        return true;
    }
    if (agent.indexOf('win64') >= 0 || agent.indexOf('wow64') >= 0) {
        return true;
    }

    return false;
};

export const parsePathToObject = (url: string) => {
    const params: Record<string, string> = {};
    const reg = /([^?=&]+)=([^?=&]+)/g;
    url.replace(reg, (match: string, key: string, value: string) => {
        params[key] = value;
        return match;
    });
    return params;
};

export const isHttpOrHttpsUrl = (url: string) => {
    return /^(http|https):\/\//.test(url);
};

export const getHworkPlatform = () => {
    if (isHworkPC()) {
        return 'pc';
    }
    if (isHworkMobile()) {
        return 'mobile';
    }
    return 'web';
};

export function copyElementText(el1: HTMLElement) {
    const el = el1.cloneNode(true);
    const cardElements = el.getElementsByClassName('message-card');
    for (let i = 0; i < cardElements.length; i++) {
        const cardElement = cardElements[i] as HTMLElement;
        cardElement.innerHTML = '[卡片]';
    }
    const operationsElements = el.querySelectorAll(
        '.message-operations-wrapper,.message-operations-mobile-wrapper',
    );
    for (let i = 0; i < operationsElements.length; i++) {
        const operationsElement = operationsElements[i] as HTMLElement;
        operationsElement.innerHTML = '';
    }
    el.style = {
        margin: '0',
        padding: '0',
        width: '0',
        height: '0',
        overflow: 'hidden',
    };
    document.body.appendChild(el);
    var body = document.body,
        range,
        sel;

    if (document.createRange && window.getSelection) {
        range = document.createRange();

        sel = window.getSelection();

        sel?.removeAllRanges();

        try {
            range.selectNodeContents(el);

            sel?.addRange(range);
        } catch (e) {
            range.selectNode(el);

            sel?.addRange(range);
        }
        document.execCommand('copy');
        //取消文本选中状态
        window.getSelection?.()?.empty();
        // toastr.success('成功复制到剪贴板！');
        // alert("成功复制到剪贴板");
    }
    document.body.removeChild(el);
}

export function getElementText(el1: HTMLElement) {
    const el = el1.cloneNode(true);
    const cardElements = el.getElementsByClassName('message-card');
    for (let i = 0; i < cardElements.length; i++) {
        const cardElement = cardElements[i] as HTMLElement;
        cardElement.innerHTML = '[卡片]';
    }
    const operationsElements = el.querySelectorAll(
        '.message-operations-wrapper,.message-operations-mobile-wrapper',
    );
    for (let i = 0; i < operationsElements.length; i++) {
        const operationsElement = operationsElements[i] as HTMLElement;
        operationsElement.innerHTML = '';
    }
    el.style = {
        margin: '0',
        padding: '0',
        width: '0',
        height: '0',
        overflow: 'hidden',
    };
    document.body.appendChild(el);
    var body = document.body,
        range,
        sel;

    let text: string = '';

    if (document.createRange && window.getSelection) {
        range = document.createRange();

        sel = window.getSelection();

        sel?.removeAllRanges();

        try {
            range.selectNodeContents(el);

            sel?.addRange(range);
        } catch (e) {
            range.selectNode(el);

            sel?.addRange(range);
        }
        text = window.getSelection()?.toString() ?? '';
        //取消文本选中状态
        window.getSelection?.()?.empty();
        // toastr.success('成功复制到剪贴板！');
        // alert("成功复制到剪贴板");
    }
    document.body.removeChild(el);
    return text;
}

const handleALinkClick = (e: Event) => {
    const target = e.target as HTMLElement;
    if (target.tagName !== 'A') return;
    e.preventDefault();
    const url = target.getAttribute('href') || '';
    // 联网搜索链接
    const isDataLink = target.hasAttribute('data-link');

    if (!isHttpOrHttpsUrl(url) || (isMobile() && !isDataLink)) return;

    if (isMobile() && isDataLink) {
        return appLink.handleMobileSearchLink(url);
    }

    if (isHworkDesktop()) return appLink.handleDesktopLinkToWeb(url);

    window.open(url, '_blank');
};

export function processAlink(element: HTMLElement) {
    element.addEventListener('click', handleALinkClick, {
        capture: true,
    });
    return () => {
        element.removeEventListener('click', handleALinkClick, {
            capture: true,
        });
    };
}

// 提取 hash 路由中的 URL 参数
export const getUrlParams = () => {
    const hash = window.location.hash; // "#/chat/CG-8502e20f-a6c5-44a4-9b71-2acfde8467a6?agent=super&tab=chat"

    // 提取 '?' 后的内容
    const queryIndex = hash.indexOf('?');
    const queryString = queryIndex !== -1 ? hash.substring(queryIndex + 1) : ''; // "agent=super&tab=chat"

    const result: Record<string, string> = {};
    if (queryString) {
        const params = queryString.split('&');
        params.forEach((param) => {
            const [key, value] = param.split('=');
            result[key] = decodeURIComponent(value);
        });
    }
    return result;
};
