<template>
    <a-row
        type="flex"
        align="middle"
        class="chat-card"
        :class="{ 'is-active': isActive }"
        ref="chatCardRef"
    >
        <a-col flex="30px">
            <a-tooltip :title="chatGroup.groupName">
                <div
                    class="chat-icon"
                    :style="{ backgroundColor: chatGroup?.iconBackground }"
                >
                    <img
                        :src="chatGroup.imgUrl"
                        :width="chatGroup?.iconBackground ? 12 : 20"
                        :height="chatGroup?.iconBackground ? 12 : 20"
                    />
                </div>
            </a-tooltip>
        </a-col>
        <a-col flex="auto" class="chat-content">
            <div class="chat-content-title">
                <a-typography-text
                    v-if="!isEdit"
                    class="chat-content-title-text"
                    :class="{ 'chat-content-title-text-colorful': colorful }"
                    :ellipsis="{ tooltip: chatGroup.groupName }"
                    :content="chatGroup.groupName"
                    :text="chatGroup.groupName"
                ></a-typography-text>
                <a-input
                    v-else
                    v-model:value="rename"
                    ref="inputRef"
                    @blur="handlerSave"
                />
            </div>
            <!-- <div class="chat-content-desc">
				{{ chatGroup.groupDesc }}
			</div> -->
            <!-- <h-icon-arrow-rise
                v-if="chatGroup.weight > 0"
                class="chat-content-extra"
            ></h-icon-arrow-rise> -->
            <img
                v-if="chatGroup.weight > 0"
                class="chat-content-extra"
                src="@/assets/icons/icon-pin.svg"
            />
            <a-dropdown v-if="!noExtra" overlayClassName="chat-operate-menu">
                <h-icon-more-vertical
                    v-if="!props.isInSearch"
                    class="more-btn"
                />
                <template #overlay>
                    <a-menu>
                        <!-- <a-menu-item>
                            <a class="operate-btn" @click="handlerEdit">
                                <img :src="topUpIcon" />
                                置顶
                            </a>
                        </a-menu-item>
                        <a-menu-item @click="$emit('delete')">
                            <a class="operate-btn">
                                <img :src="deleteIcon" />
                                删除
                            </a>
                        </a-menu-item> -->
                        <a-menu-item v-if="chatGroup.weight > 0">
                            <a class="operate-btn" @click="handleUnpin">
                                <!-- <h-icon-edit /> -->
                                <img
                                    class="operate-btn-icon"
                                    src="@/assets/icons/icon-unpin.svg"
                                />
                                取消置顶
                            </a>
                        </a-menu-item>
                        <a-menu-item v-else>
                            <a class="operate-btn" @click="handlePin">
                                <!-- <h-icon-edit /> -->
                                <img
                                    class="operate-btn-icon"
                                    src="@/assets/icons/icon-pin.svg"
                                />
                                置顶
                            </a>
                        </a-menu-item>
                        <a-menu-item @click="handleRemove">
                            <a class="operate-btn">
                                <h-icon-delete class="operate-btn-icon" />
                                移除
                            </a>
                        </a-menu-item>
                    </a-menu>
                </template>
            </a-dropdown>
        </a-col>
    </a-row>
</template>
<script setup lang="ts">
import '@hwork/icon/more-vertical';
import '@hwork/icon/edit';
import '@hwork/icon/delete';
import '@hwork/icon/arrow-rise';
import '@hwork/icon/arrow-rise';
import { ref, toRefs, computed, nextTick, watch } from 'vue';
import type { ComponentPublicInstance } from 'vue';
import { useRoute } from 'vue-router';
import { ChatGroup } from '@/api/chat';

const route = useRoute();
const props = defineProps<{
    chatGroup: ChatGroup;
    isInSearch: boolean;
    noExtra?: boolean;
    colorful?: boolean;
}>();
const emit = defineEmits<{
    (e: 'edit', code: string, rename: string): void;
    (e: 'delete'): void;
    (e: 'scrollTo', top: number): void;
    (e: 'pin', code: string): void;
    (e: 'unpin', code: string): void;
    (e: 'remove', code: string): void;
}>();
const { chatGroup } = toRefs(props);
const isEdit = ref(false);
const rename = ref('');
const inputRef = ref<HTMLElement>();
const chatCardRef = ref<ComponentPublicInstance>();
const isActive = computed(() => {
    const code = route.params.code;
    return code === chatGroup.value.groupCode;
});
const handlerEdit = () => {
    rename.value = props.chatGroup.groupDesc;
    isEdit.value = true;
    nextTick(() => {
        inputRef.value?.focus();
    });
};
const handlerSave = () => {
    isEdit.value = false;
    if (rename.value === props.chatGroup.groupDesc) return;
    emit('edit', props.chatGroup.groupCode, rename.value);
};

function handlePin() {
    emit('pin', props.chatGroup.groupCode);
}

function handleUnpin() {
    emit('unpin', props.chatGroup.groupCode);
}

function handleRemove() {
    emit('remove', props.chatGroup.groupCode);
}

watch(
    () => isActive.value,
    (val) => {
        if (val) {
            nextTick(() => {
                const { height } =
                    chatCardRef.value?.$el.offsetParent.getBoundingClientRect();
                const top = chatCardRef.value?.$el.offsetTop;
                if (top >= height) {
                    emit('scrollTo', chatCardRef.value?.$el.offsetTop);
                }
            });
        }
    },
    {
        immediate: true,
    },
);
</script>
