<template>
    <div class="chat-card-container">
        <a-row
            type="flex"
            class="chat-card"
            :style="{ left: `${left}px` }"
            @touchstart="touchstartEvent"
            @touchend="touchendEvent"
            @click="$emit('jump')"
        >
            <a-col flex="30px">
                <div
                    class="chat-icon"
                    :style="{ backgroundColor: chatGroup?.iconBackground }"
                >
                    <img
                        :src="chatGroup.imgUrl"
                        :width="chatGroup?.iconBackground ? 12 : 20"
                        :height="chatGroup?.iconBackground ? 12 : 20"
                    />
                </div>
            </a-col>
            <a-col flex="auto" class="chat-content">
                <div class="chat-content-title">
                    <a-typography-text
                        :ellipsis="{ tooltip: chatGroup.groupName }"
                        :content="chatGroup.groupName"
                    ></a-typography-text>
                </div>
                <div class="chat-content-desc">
                    <a-typography-text
                        class="desc"
                        :ellipsis="{ tooltip: chatGroup.groupDesc }"
                        :content="chatGroup.groupDesc"
                    ></a-typography-text>
                </div>
            </a-col>
        </a-row>
        <div v-show="showDelete" class="chat-operate-container">
            <a class="operate-btn" @click="$emit('delete')">删除</a>
        </div>
    </div>
</template>
<script setup lang="ts">
import '@hwork/icon/more';
import '@hwork/icon/edit';
import '@hwork/icon/delete';
import { ref, toRefs } from 'vue';
import { ChatGroup } from '@/api/chat';
const props = defineProps<{
    chatGroup: ChatGroup;
}>();
const { chatGroup } = toRefs(props);
const startX = ref(0);
const showDelete = ref(false);
const left = ref(0);
const touchstartEvent = (e: any) => {
    startX.value = e.changedTouches[0].clientX;
};
const touchendEvent = (e: any) => {
    const endX = e.changedTouches[0].clientX;
    if (startX.value - endX >= 30) {
        showDelete.value = true;
        left.value = -60;
    }
    if (startX.value - endX <= -30) {
        showDelete.value = false;
        left.value = 0;
    }
};
</script>
<style lang="less" scoped>
.chat-card-container {
    position: relative;
    height: 69px;
    margin-top: 10px;
}
.chat-card {
    position: absolute;
    width: 100%;
    top: 0;
    height: 69px;
    padding: 12px;
    border-radius: 8px;
    background-color: #fff;
    box-sizing: border-box;
    cursor: pointer;
    transition: left 0.3s ease-in-out;
    .chat-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 20px;
        height: 20px;
        vertical-align: top;
        border-radius: 6px;
    }
    .chat-content {
        position: relative;
        width: 0;
        .chat-content-title {
            font-size: 16px;
            font-weight: 600;
            line-height: 20px;
            padding-right: 16px;
        }
        .chat-content-desc {
            margin-top: 8px;
            font-size: 14px;
            .desc {
                color: #86909c;
            }
        }
    }
}
.chat-operate-container {
    display: flex;
    justify-content: flex-end;
    height: 69px;
    border-radius: 8px;
    background: #f53f3f;
    .operate-btn {
        display: inline-flex;
        width: 60px;
        color: #fff;
        align-items: center;
        justify-content: center;
    }
}
</style>
