import Statistics, { setUser } from '@deep/statistics-vue';
import { App } from 'vue';
import type { Router } from 'vue-router';

class SentryPlugin {
    init(app: App, router: Router) {
        if (!this.dsnUrl) return;

        new Statistics({
            app,
            router,
            sentry: {
                dsn: this.dsnUrl,
                environment: this.env,
                release: process.env.VUE_APP_FRAPPE_VERSION,
                tracePropagationTargets: [/^\/(?!\/)api/],
            },
        });
    }
    setUserInfo(id: string, name: string) {
        if (!this.dsnUrl) return;

        setUser({
            id,
            name,
        });
    }

    get env(): string {
        return process.env.VUE_APP_RUN_ENV;
    }

    get dsnUrl(): string {
        return process.env.VUE_APP_SENTRY_DSN || '';
    }
}

export default new SentryPlugin();
