import deepRequest from '@deep/request'
import { DeepRequestStatic, RequestConfig } from '@deep/request-types';
import { notification } from '@hwork/ant-design-vue';
import { getContainer, getTokenAsync } from '@/utils/util';

notification.config({
    getContainer: getContainer,
});
// const deepRequest: DeepRequestStatic = window.$bridge.request;
deepRequest.config({
    baseURL: process.env.VUE_APP_API_BASE_URL,
    timeout: 20000,
    getResponse: true,
    withToken: false,
});

deepRequest.addRequestInterceptor(async function (response) {
    const token = await getTokenAsync();
    if (!token) {
        // toLogin();
        return response;
    }
    (response as any).headers = {
        ...(response.headers || {}),
        Authorization: `Bearer ${token}`,
    };
    return response;
});

const NO_AUTH_CODE = [40001, '40001'];
const getErrorCode = (code: any) => {
    if (code === 'ECONNABORTED') {
        return '请求超时';
    }
    return code || '请求异常';
};
const whiteList: string[] = [
    '/api/agent-manager/v1/agent/market/detail/'
]
// 添加响应拦截器
deepRequest.addResponseInterceptor(
    function (response) {
        if (response?.data) {
            // 额外检查 response === 40001
            if (NO_AUTH_CODE.includes(response?.data?.code)) {
                // toLogin();
            }
            const url = response.config.url as string;
            const pass = whiteList.findIndex((item) => {
                return url.includes(item);
            });
            if (pass === -1 && response.data?.code !== 10000 && response.data?.code !== 200) {
                notification.error({
                    message: '提示',
                    description:
                        response.data?.msg || response.data?.message || '请求异常',
                });
            }
            return response as any;
        }
    },
    function (error) {
        if (error instanceof Error) {
            return Promise.reject(error);
        } else {
            // 登录校验
            const status = error.response?.status || getErrorCode(error.code);

            switch (status) {
                case 401: {
                    // toLogin();
                    break;
                }
            }
        }
    },
);

function request<Data>(
    url: string | RequestConfig,
    config?: RequestConfig,
): Promise<Data> {
    return deepRequest<Data>(url, config).then((response) => {
        return (response as any).data;
    });
}

export default request;
