import request from '@/utils/request';

export type UserInfoType = {
    userPhoto: string;
    userCode: string;
    userName: string;
};

export function getUserInfo() {
    return request<Res<UserInfoType>>({
        baseURL: process.env.VUE_APP_API_HWORK_BASE_URL,
        url: '/domain/api/v2/me',
    });
}
